.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($main-color1-darker, 0.9);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s;
  z-index: 999;
  .custom-modal {
    position: absolute;
    display: block;
    top: 7%;
    background-color: white;
    box-shadow: 0 0 18px 0 rgba(0, 0, 0, 0.4);
    transition: margin 0.2s;
    .close-button {
      position: absolute;
      left: 100%;
      bottom: 100%;
      margin-left: 10px;
      margin-bottom: 10px;
      font-size: 30px;
      cursor: pointer; }
    .content-modal {
      padding: 15px;
      overflow-y: initial !important; } } }

.modal-body {
  height: 350px;
  overflow-y: auto; }

@include mQuery(479px) {
  .modal-container {
    .custom-modal {
      .close-button {
        left: 1%;
        bottom: 85%; } } } }

//Javascript Interactions
//deze class modal-open wordt toegevoegd aan
//de class modal-container via de js functie
//in custom-modal.js
.modal-container {
  &.modal-open {
    opacity: 1;
    pointer-events: all;
    .modal {
      margin-top: -180px; } } }

.conditions {
  margin-top: 30px;
  cursor: pointer;
  transition: all 0.5s ease;
  &:hover {
    color: $main-color1-darker; } }

@include mQuery(479px) {
  .conditions {
    margin-bottom: 30px; } }
