// CTA button on header region

.header-front {
  .cta {
    z-index: 999;
    position: absolute;
    top: 150px;
    right: 15px;
    .btn {
      background: $main-color2;
      font-family: robotobold, sans-serif;
      font-size: 1.5em;
      padding: 25px;
      border: none; }
    a {
      color: white;
      text-decoration: none;
      &:hover {
        text-decoration: none;
        color: white;
        filter: brightness(90%);
        transition: all 0.3s ease-in-out; } } } }

.header-page {
  .cta {
    display: none; } }

@include mQuery(479px) {
  .cta {
    top: 40px;
    .btn {
      font-size: 1em;
      padding: 10px; } } }
