// Forms
// input.html.twig - select.html.twig - form-element-label.html.twig

// Form components
.form_input-extend {
  border: 1px solid $main-color1-darker;
  border-radius: 0;
  background: white; }

.main-content {
  .form-input {
    padding: 5px;
    width: 500px;
    display: inline-block;

    @extend .form_input-extend; }
  .form-submit {
    background: $main-color1-darker; }

  textarea {
    @extend .form_input-extend;
    background: white; } }


input[type="radio"], input[type="checkbox"] {
  background: white; }

.form-number {
  width: 300px;
  @extend .form_input-extend; }

.form-select {
  @extend .form_input-extend;
  padding: 4px; }

.form-item {
  width: 100%; }

.form_label-extend {
  background: $main-color1-darker;
  color: white; }

.form-submit {
  @extend .form_label-extend;
  transition: color 0.5s ease;
  &:hover {
    color: $main-color2; } }


.control-label {
  @extend .form_label-extend;
  padding: 6px;
  font-family: robotolight, sans-serif;
  font-weight: normal;
  display: inline-block;
  float: left;
  width: 170px; }

// contact form in de footer
.footer_contact_form-wrap {
  .control-label {
    width: 120px; }
  .form-item {
    .form-input {
      width: 200px;
      background: white; } }
  .form-submit {
    border: solid 1px white;
    padding: 5px 20px;
    color: white;
    background: $main-color1-darker; }
  input, textarea {
    border: solid 1px $main-color1-darker;
    color: $main-color1-darker;
    background: white; }
  textarea {
    height: 50px; } }

// .webform-button--submit
