// Class wide-option ingesteld in de UI van het field option adres = gegevens klant
// bij het onderdeel 'Wrapper-attributen
// dit om het een afwijkende breedte mee te geven tov 'soort transport'
// idem voor algemene voorwaarden gelezen
.wide-option {
  .option {
    width: 500px; } }

.input-radio, .input-checkbox {
  width: 45px;
  display: inline-block;
  float: left;
  margin: 10px 0 0!important;
  cursor: pointer; }

@include mQuery(479px) {
  .control-label {
    padding: 7px;
    font-size: 0.85em;
    width: 120px; }
  .main-content {
    .form-input {
      width: 220px; } }
  .wide-option {
    .option {
      width: 300px; } } }

@include mQuery(361px) {
  .main-content {
    .form-input {
      width: 200px; } }
  .wide-option {
    .option {
      width: 270px; } } }

@include mQuery(321px) {
  .main-content {
    .form-input {
      width: 160px; } }
  .wide-option {
    .option {
      width: 230px; } }
  .input-radio {
    width: 30px; }
  .option {
    width: 100px; } }
